import Avatar from "@material-ui/core/Avatar";
import Box from "@material-ui/core/Box";

export const User = ({user}) => {
    return (
        <Box display='flex' alignItems='center'>
            <Avatar src={user?.avatar} alt={user?.name}/>
            <span style={{padding: 5}}>
                {user?.name}
            </span>
        </Box>
    )
}